const accordanceCategory = (main, additional) => {
    if(main === ''){
        if(additional === '') return 6
        if(additional ==='') return 7
        if(additional ==='') return 8
    }
    if(main === ''){
        if(additional === '') return 10
        if(additional ==='') return 11
        if(additional ==='') return 12
    }
    if(main === ''){
        if(additional === '') return 14
        if(additional ==='') return 15
    }
}

export const reverseAccordance = number => {
    switch (number) {
        case 6: return ', '
        case 7: return ', '
        case 8: return ', '
        case 10: return ', '
        case 11: return ', '
        case 12: return ', '
        case 14: return ',   '
        case 15: return ', '
    }
}

export default accordanceCategory